/* F O O T E R */
.footer {
  background-color: #757575;
  margin-top: 50px;

  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 10px 0;
    column-gap: 15px;
  }

  &__item {
    cursor: pointer;
    padding: 15px 0;
    color: black;
  }

  &__item:hover {
    color: #ffffff;
  }
}