.works {
  &__heading {
    margin-bottom: 35px;
    margin-top: 20px;
    color: #757575;
  }

  &-list {
    display: flex;
    column-gap: 25px;
    flex-wrap: wrap;
  }

  &__item {
    margin-bottom: 20px;
    /* width: 200px; */
  }

  &__link {
    display: inline-block;
    position: relative;
    opacity: 0.7;
  }

  &__link:hover {
    opacity: 1;
  }

  .--effect-1 {
    background-color: inherit;
  }
  
  .--effect-1 a {
    padding: 0 20px;
    height: 45px;
    /* color: #212121; */
    line-height: 45px;
  
  }
  
  .--effect-1 a::before,
  .--effect-1 a::after {
    position: absolute;
    width: 45px;
    height: 2px;
    background-color: #212121;
    content: "";
    opacity: 0.2;
    transition: all 0.1s;
  }
  
  .--effect-1 a::before {
    top: 0;
    left: 0;
    transform: rotate(90deg);
    transform-origin: 0 0;
  }
  
  .--effect-1 a::after {
    right: 0;
    bottom: 0;
    transform: rotate(90deg);
    transform-origin: 100% 100%;
  }
  
  .--effect-1 a:hover::before {
    width: 80%;
    height: 2px;
    left: 50%;
    opacity: 1;
    transform: rotate(0deg) translateX(-50%);
  }
  
  .--effect-1 a:hover::after {
    width: 80%;
    height: 2px;
    right: 50%;
    opacity: 1;
    transform: rotate(0deg) translateX(50%);
  }
  
}

