@import "parts/_header.scss";
@import "parts/_font-face.scss";
@import "parts/_footer.scss";
@import "parts/_palette";
// @import "parts/_slider";
@import "parts/_works.scss";
@import "parts/_slide.scss";

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Базовые стили */
.body {
  // font-family: 'Open Sans', sans-serif;
  font-size: 20px;
  line-height: 24px;
  color: #212121;
  background-color: #BDBDBD;
  display: grid;
  grid-template-rows: min-content auto min-content;
  height: 100vh;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}

.container {
  margin: 0 auto;
  max-width: 1900px;
  padding: 0 15px;
}

.container--inner {
  padding: 0 20px;
}



/* M A I N */
.main {
  .heading {
    margin-bottom: 50px;
    text-align: center;
    font-size: 42px;
    line-height: 1;
  }

  .breadcumbs {
    font-size: 12px;
    display: flex;
    flex-wrap: wrap;
    column-gap: 25px;
    margin-left: 10px;
    margin-bottom: 15px;
  }

  .breadcrumbs-item {
    position: relative;
    text-decoration: underline;
  }

  .breadcrumbs-item:hover {
    text-decoration: none;
  }

  .breadcrumbs-item::before {
    content: "";
    position: absolute;
    width: 6px;
    height: 6px;
    background-color: #212121;
    left: -10px;
    top: 8px;
    transform: rotate(45deg);
  }

  .breadcumbs .breadcrumbs-item:last-of-type {
    opacity: 0.5;
    text-decoration: none;
  }


  
  .--effect-1 {
    background-color: inherit;
  }

  .--effect-1 a {
    padding: 0 20px;
    height: 45px;
    /* color: #212121; */
    line-height: 45px;

  }

  .--effect-1 a::before,
  .--effect-1 a::after {
    position: absolute;
    width: 45px;
    height: 2px;
    background-color: #212121;
    content: "";
    opacity: 0.2;
    transition: all 0.1s;
  }

  .--effect-1 a::before {
    top: 0;
    left: 0;
    transform: rotate(90deg);
    transform-origin: 0 0;
  }

  .--effect-1 a::after {
    right: 0;
    bottom: 0;
    transform: rotate(90deg);
    transform-origin: 100% 100%;
  }

  .--effect-1 a:hover::before {
    width: 80%;
    height: 2px;
    left: 50%;
    opacity: 1;
    transform: rotate(0deg) translateX(-50%);
  }

  .--effect-1 a:hover::after {
    width: 80%;
    height: 2px;
    right: 50%;
    opacity: 1;
    transform: rotate(0deg) translateX(50%);
  }
}